import MasterPage from '../components/Master';
import fetch from 'isomorphic-unfetch';
import Precio from '../components/Precio';
import Noticias from '../components/Noticias';
const Index = (props) => (
  <MasterPage>
    <div className="row">
      <div className="col-12">
        <h2>Precio del bitcoin</h2>
        <Precio precio={props.precioBitcoin}/>
      </div>
      <div className="col-md-12">
      <h2>Noticias sobre bitcoin</h2>
      <Noticias noticias={props.noticias}/>
      </div>
      {/* <div className="col-md-4">
      <h2>Proximos eventos bitcoin</h2>
      </div> */}
    </div>
  </MasterPage>
);

Index.getInitialProps = async () => {
  const header = {
    method: 'GET',
    headers: {'X-CMC_PRO_API_KEY': 'bb177da9-0c58-44be-81c6-ab109f322325'}
  };
  const precio = await fetch('https://pro-api.coinmarketcap.com/v1/cryptocurrency/quotes/latest?id=1', header);
  const noticias = await fetch('http://newsapi.org/v2/everything?q=bitcoin&from=2020-03-06&sortBy=publishedAt&apiKey=8acedf3fde9d4c5eb02e436e5eaf6635&language=es');
  const restPrecio = await precio.json();
  const restNoticias = await noticias.json();
  return {
    precioBitcoin: restPrecio.data[1].quote.USD,
    noticias: restNoticias.articles
  }
}

export default Index;
